"use client";

import { useSession } from "next-auth/react";
import { useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { PlusCircle, FolderKanban } from "lucide-react";

interface Project {
  id: string;
  name: string;
  description: string | null;
}

export default function DashboardPage() {
  const { data: session, status } = useSession();
  const router = useRouter();
  const [projects, setProjects] = useState<Project[]>([]);
  const [loadingProjects, setLoadingProjects] = useState(true);

  useEffect(() => {
    if (status === "unauthenticated") {
      router.push("/");
    }
  }, [status, router]);

  useEffect(() => {
    if (session?.user?.id) {
      const fetchProjects = async () => {
        try {
          const response = await fetch(`/api/projects?userId=${session.user.id}`);
          if (!response.ok) {
            throw new Error("Failed to fetch projects");
          }
          const data = await response.json();
          setProjects(data);
        } catch (error) {
          console.error("Error fetching projects:", error);
        } finally {
          setLoadingProjects(false);
        }
      };
      fetchProjects();
    }
  }, [session?.user?.id]);

  if (status === "loading" || loadingProjects) {
    return (
      <div className="flex h-screen items-center justify-center bg-slate-50">
        <p className="text-lg text-slate-500">Cargando Dashboard...</p>
      </div>
    );
  }

  if (!session) {
    return null;
  }

  return (
    <div className="min-h-screen bg-slate-50">
      {/* Header */}
      <div className="bg-slate-800 px-6 py-6 mb-8">
        <div className="max-w-7xl mx-auto">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-3xl font-bold text-white mb-1">Mis Proyectos</h1>
              <p className="text-gray-300 text-sm">Gestiona y organiza todos tus proyectos</p>
            </div>
            <Link href="/projects/new" passHref>
              <Button className="bg-blue-600 hover:bg-blue-700 text-white">
                <PlusCircle className="mr-2 h-4 w-4" /> Nuevo Proyecto
              </Button>
            </Link>
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="max-w-7xl mx-auto px-6 pb-12">
        {projects.length === 0 ? (
          <div className="flex flex-col items-center justify-center py-20">
            <div className="bg-white rounded-2xl shadow-lg border border-gray-200 p-12 text-center max-w-md">
              <div className="w-16 h-16 bg-blue-50 rounded-full flex items-center justify-center mx-auto mb-4">
                <FolderKanban className="w-8 h-8 text-blue-600" />
              </div>
              <h2 className="text-2xl font-bold text-slate-900 mb-2">
                Comienza tu primer proyecto
              </h2>
              <p className="text-slate-500 mb-6">
                Aún no tienes proyectos. ¡Crea uno para empezar a organizar tu trabajo!
              </p>
              <Link href="/projects/new" passHref>
                <Button size="lg" className="bg-blue-600 hover:bg-blue-700 text-white">
                  <PlusCircle className="mr-2 h-5 w-5" /> Crear mi primer proyecto
                </Button>
              </Link>
            </div>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {projects.map((project) => (
              <Link key={project.id} href={`/projects/${project.id}`} passHref>
                <div className="bg-white rounded-xl shadow-md border border-gray-200 p-6 hover:shadow-xl hover:border-blue-600 transition-all duration-200 cursor-pointer group">
                  <div className="flex items-start justify-between mb-3">
                    <div className="w-12 h-12 bg-gradient-to-br from-blue-600 to-blue-700 rounded-lg flex items-center justify-center">
                      <FolderKanban className="w-6 h-6 text-white" />
                    </div>
                  </div>
                  <h2 className="text-xl font-bold text-slate-900 mb-2 group-hover:text-blue-600 transition-colors">
                    {project.name}
                  </h2>
                  <p className="text-slate-500 text-sm line-clamp-2">
                    {project.description || "Sin descripción"}
                  </p>
                </div>
              </Link>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}
